/**
 * Obtiene una respuesta generada por IA para un certificado/curso y red social dada,
 * utilizando la llamada AJAX `local_socialcert_get_ai_response`.
 *
 * La funci√≥n env√≠a un payload con `certname`, el curso fijo "Curso de Python",
 * la organizaci√≥n y la red social, y resuelve con el texto de la respuesta (`reply`)
 * devuelta por el backend.
 *
 * @function ai_response
 * @async
 * @param {string} certname - Nombre del certificado (o del estudiante) a incluir en el prompt.
 * @param {string} course - Nombre del certificado (o del estudiante) a incluir en el prompt.
 * @param {string} org - Nombre de la organizaci√≥n o instituci√≥n emisora.
 * @param {string|string[]} socialmedia - Red social objetivo (p. ej., "LinkedIn") o lista de redes.
 * @returns {Promise<string>} Promesa que se resuelve con el contenido textual de la respuesta de IA.
 * @throws {SyntaxError} Si el JSON devuelto por el backend no es v√°lido.
 * @throws {Error} Si la llamada AJAX falla por cualquier motivo (se notificar√° con `Notification.exception`).
 *
 * @example
 * ai_response("Certificado de Anal√≠tica", "BUEN DATA", "LinkedIn")
 *   .then((reply) => {
 *     console.log("Respuesta IA:", reply);
 *   })
 *   .catch((err) => {
 *     console.error("Error obteniendo la respuesta de IA:", err);
 *   });
 */
function ai_response (certname, course, org, socialmedia) {
  return new Promise((resolve, reject) => {
    Ajax.call([{
      methodname: 'local_socialcert_get_ai_response',
      args: {
        body: {
          certname: certname,
          course: course,
          org: org,
          socialmedia: socialmedia
        }
      },
    }])[0].then((response) => {
      try {
        const parsed = JSON.parse(response.json);
        return resolve(parsed.reply);
      } catch (e) {
        reject(e);
      }
    }).catch((err) => {
      Notification.exception(err);
      reject(err);
    });
  });
}


/**
 * Ejecuta/Detiene la ‚ÄúIA‚Äù (streaming).
 * Lee data-target, data-mode ("char"|"word"), data-speed (ms).
 * @param {MouseEvent} ev
 * @param {HTMLElement} btn
 */
export function runAiHandler(ev, btn) {
  ev.preventDefault();

  // Localiza el destino
  const sel = btn.dataset.target;
  let target = null;
  if (sel) {
    target = document.querySelector(sel);
  } else {
    const wrap = btn.closest('.lsc-response-wrap');
    target = wrap ? wrap.querySelector('.lsc-response') : null;
  }
  if (!target) { return; }

  // Si ya hay stream en ese destino: detener y restaurar
  if (streams.has(target)) {
    const s = streams.get(target);
    if (s && s.stop) { s.stop(); }
    btn.disabled = false;
    btn.textContent = getString('airesponsebtn', 'local_socialcert');
    return;
  }

  // Preparar UI
  const mode = (btn.dataset.mode === 'char') ? 'char' : 'word';
  const speed = parseInt(btn.dataset.speed || '40', 10);
  const certname = btn.dataset.certname || '';
  const course = btn.dataset.course || '';
  const org = btn.dataset.org || '';
  const socialmedia = btn.dataset.socialmedia || '';
  const original = btn.textContent;
  btn.disabled = true;
  btn.textContent = 'Generating';
  target.setAttribute('aria-busy', 'true');
  target.setAttribute('role', 'status');

  const loader = document.getElementById('ai-card');

  ai_response(certname, course, org, socialmedia).then((fulltext) => {

    loader.classList.add('hidden');
    loader.setAttribute('aria-busy', 'false');
    const stream = typewriter(target, fulltext , mode, speed);
    stream.done.then(() => {
      btn.disabled = false;
      btn.textContent = original;
      target.removeAttribute('aria-busy');
      streams.delete(target);
    });
  }).catch(() => {
    btn.disabled = false;
    btn.textContent = original;
    target.removeAttribute('aria-busy');
  });
}

/**
 * Copia al portapapeles:
 *  - el valor expl√≠cito en data-copy-value, o
 *  - si no est√°, el texto visible del elemento.
 * Muestra feedback breve en el chip de copiar si existe.
 * @param {MouseEvent|KeyboardEvent} ev
 * @param {HTMLElement} el
 */
export function handleCopyText(ev, el) {
  // Soporta acticonst con Enter/Espacio si llega por teclado
  if (ev.type === 'keydown') {
    const code = ev.key || ev.code;
    if (code !== 'Enter' && code !== ' ' && code !== 'Spacebar') { return; }
    ev.preventDefault();
  }

  let value = el.getAttribute('data-copy-value');
  if (!value) {
    // Toma el texto visible; ignora el icono de copiar
    const clone = el.cloneNode(true);
    const btn = clone.querySelector('.lsc-copybtn');
    if (btn) { btn.remove(); }
    value = clone.innerText || clone.textContent || '';
  }

  navigator.clipboard.writeText(value).then(function () {
    // Feedback: swap temporal en el chip si est√°
    let badge = el.querySelector('.lsc-copybtn');
    if (!badge) { return; }
    const original = badge.textContent;
    badge.textContent = '‚úî';
    setTimeout(function () { badge.textContent = original; }, 900);
  }).catch(function () {
    // Silencioso; podr√≠as mostrar \core\notification desde PHP si lo prefieres
  });
}

/**
 * Registra una acci√≥n para usar con data-action="name".
 * @param {string} name
 * @param {(ev:Event, el:HTMLElement)=>void} fn
 * @returns {void}
 */
export function register(name, fn) { registry.set(name, fn); }

/**
 * Descarga la imagen indicada por data-url o por el src de data-target.
 * A√±ade forcedownload=1 para pluginfile.php (Moodle) para forzar la descarga.
 * @param {MouseEvent} _ev
 * @param {HTMLElement} el
 */
function handleDownloadImage(_ev, el) {
  // 1) Resolver URL
  let url = el.getAttribute('data-url') || '';

  // eslint-disable-next-line no-console
  console.log('URL1:', url);

  if (!url) {
    const sel = el.getAttribute('data-target') || '';
    const img = sel ? document.querySelector(sel) : null;
    if (img) { url = img.getAttribute('src') || ''; }
  }
  if (!url) { return; }

  // 2) Si es pluginfile.php, a√±ade forcedownload=1
  try {
    const u = new URL(url, window.location.origin);
    if (u.pathname.indexOf('/pluginfile.php') !== -1 && !u.searchParams.has('forcedownload')) {
      u.searchParams.set('forcedownload', '1');
      url = u.toString();
      // eslint-disable-next-line no-console
      console.log('URL: ', url);
    }
  } catch (_e) { /* usa url tal cual */ }

  // 3) Nombre de archivo
  const filename = el.getAttribute('data-filename') || (function () {
    try {
      const u2 = new URL(url, window.location.origin);
      const base = u2.pathname.split('/').pop() || 'certificado';
      return base.indexOf('.') === -1 ? (base + '.png') : base;
    } catch (_e) { return 'certificado.png'; }
  })();

  // 4) Intento principal: descargar como blob (mejor UX)
  fetch(url, { credentials: 'same-origin' })
    .then(function (res) {
      if (!res.ok) { throw new Error('HTTP ' + res.status); }
      return res.blob();
    })
    .then(function (blob) {
      const blobUrl = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = blobUrl;
      a.download = filename;           // fuerza descarga con nombre
      document.body.appendChild(a);
      a.click();
      a.remove();
      setTimeout(function () { URL.revokeObjectURL(blobUrl); }, 500);
    })
    .catch(function () {
      // 5) Fallback: abrir en nueva pesta√±a (usuario guarda manualmente)
      const a = document.createElement('a');
      a.href = url;
      a.target = '_blank';
      a.rel = 'noopener';
      // Si es misma-origen, download tambi√©n podr√≠a funcionar sin blob:
      a.download = filename;
      document.body.appendChild(a);
      a.click();
      a.remove();
    });
}

/**
 * Punto de entrada: registra handlers base y activa la delegaci√≥n de clicks.
 * @returns {void}
 */
export function init() {
  const root = document.querySelector('.local-socialcert');
  if (!root) {
    return;
  }

  // ‚¨áÔ∏è FALTABA registrar el selector de red
  register('select-network', handleSelectNetwork);
  updateVisibility(root);

  // Registra acciones base
  register('open-link', handleOpenLink);
  register('copy-html', handleCopyHtml);
  register('copy-image', handleCopyImage);
  register('run-ai', runAiHandler);
  register('copy-text', handleCopyText);
  register('download-image', handleDownloadImage);

  // Delegaci√≥n √∫nica para todos los clicks con data-action
  on(root, '[data-action]', 'click', (ev, el) => {
    const action = el.dataset.action;
    const fn = registry.get(action);
    if (fn) {
      fn(ev, el);
    }
  });

  on(root, '[data-action="copy-text"]', 'keydown', (ev, el) => handleCopyText(ev, el));

  // ‚¨áÔ∏è Calcula visibilidad inicial seg√∫n data-network del root
  updateVisibility(root);
}
